.container {
  display: block;
  width: 80%;
  margin: 0 auto 30px;
}
.dFlex {
  display: flex;
  width: 100%;
  justify-content: center;
}
.card {
  border: 1px solid #ccc;
  padding: 10px 20px;
  min-width: 30%;
  margin: 14px 18px;
  border-radius: 5px;
  box-sizing: border-box;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}
.card:first-child {
  margin-left: 0;
}
.card:last-child {
  margin-right: 0;
}
.cardP {
  margin: 5px 0 10px;
}
.cardH {
  margin: 10px 0 20px;
}
.blue {
  border-left: 5px solid #1565c0;
}
.yellow {
  border-left: 5px solid #fdd835;
}
.red {
  border-left: 5px solid #d84315;
}
.blue .cardH {
  color: #1565c0;
}
.yellow .cardH {
  color: #fdd835;
}
.red .cardH {
  color: #d84315;
}
.date {
    text-align: center;
    font-weight: bold;
    margin: 20px 0 10px;
    font-size: 16px;
}
.date span {
    font-weight: 400;
    font-size: 14px;
}

@media only screen and (min-width: 1024px) {
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .card {
    margin: 14px 13px;
  }
}
@media only screen and (max-width: 767px) {
    .dFlex {
        flex-wrap: wrap;
      }
      .card {
          margin: 10px 0;
          width: 100%;
      }
}
